.nav {
    grid-area: n;
    font-family: Poppins, sans-serif;
    justify-content: center;
    height: 100%;

    background: #000;
    overflow: hidden;
}

.item{
    position: relative;
    display: flex;
    flex-direction: column;
}

.item a{
    position: relative;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: 900;
    letter-spacing: 2px;
    font-size: 1.5em;
    padding: 0 10px;
    line-height: 1.2em;
    content: attr(data-text);
}

.active{
        -webkit-text-stroke: 1px #eee;

}
.friends{
    margin-top: 100px;
}
.img{
    padding: 10px;
    width: 180px;
}
.text{
    padding-left: 10px;
    color: #eeeeee;
}